@charset "UTF-8";
/* 阴影 */
.g-calendar-box, .g-calendar-box .m-rotate-drop {
  background-color: #ffffff;
  box-shadow: 1px 1px 6px 0px rgba(0, 0, 0, 0.16);
  border-radius: 4px; }

.g-calendar {
  position: relative; }
  .g-calendar .g-calendar-box {
    margin-top: 10px;
    width: 240px;
    position: absolute;
    z-index: 2; }
  .g-calendar .m-clalendar-inpBox {
    width: 100%;
    color: #535be9;
    position: relative; }

.m-clalendar-inpBox .calendar-inpTxt {
  border: 1px solid #e3e3e3;
  text-indent: 2.5em;
  cursor: pointer;
  width: 100%; }

.m-clalendar-inpBox .calendar-inp-icon {
  position: absolute;
  line-height: 34px;
  left: 10px; }

.m-clalendar-inpBox .m-search-close {
  position: absolute;
  z-index: 5;
  right: 10px;
  top: 8px;
  cursor: pointer; }

.g-calendar-box .g-calendar-view {
  width: 100%;
  position: relative;
  color: #535be9;
  font-weight: bold; }

.g-calendar-box .m-rotate-drop {
  padding: 8px;
  position: absolute;
  z-index: 6;
  margin-top: 6px;
  left: -6px;
  cursor: pointer; }
  .g-calendar-box .m-rotate-drop .active {
    color: white;
    background: #535be9; }
  .g-calendar-box .m-rotate-drop > li {
    padding: 6px; }

.g-calendar-box .m-viewOpt {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 4px 0;
  padding-left: 12px;
  border-bottom: 1px solid #535be9;
  color: #535be9; }
  .g-calendar-box .m-viewOpt .hideMoveBtn {
    visibility: hidden;
    pointer-events: none;
    opacity: 0; }
  .g-calendar-box .m-viewOpt .curViewType {
    border-radius: 100%;
    border: 1px solid;
    padding: 4px 6px;
    cursor: pointer; }
  .g-calendar-box .m-viewOpt .m-timeSel {
    cursor: pointer; }
  .g-calendar-box .m-viewOpt .m-moveBtns .s-btn {
    color: #535be9;
    background: none;
    border: none; }

.g-calendar-box .m-calendar-view {
  padding: 4px 0 8px 0;
  position: relative; }

.m-calendar-view .item-calendar-view {
  width: 100%;
  display: none; }

.m-calendar-view .view-item {
  cursor: pointer; }
  .m-calendar-view .view-item:hover span {
    background: #535be9;
    color: white; }

.m-calendar-view .view-item {
  text-align: center; }
  .m-calendar-view .view-item span {
    display: inline-block;
    border-radius: 50%; }
  .m-calendar-view .view-item .day-span {
    width: 35px;
    height: 35px;
    line-height: 35px; }
  .m-calendar-view .view-item .mon-span, .m-calendar-view .view-item .year-span {
    width: 60px;
    height: 60px;
    line-height: 60px; }
  .m-calendar-view .view-item .searson-span {
    width: 80px;
    height: 80px;
    line-height: 80px; }

.m-calendar-view .day-disabled {
  color: grey;
  cursor: not-allowed;
  text-align: center; }
  .m-calendar-view .day-disabled span {
    display: inline-block;
    width: 35px;
    height: 35px;
    line-height: 35px; }

.m-calendar-view .week-group {
  display: flex;
  color: black;
  font-weight: bolder; }
  .m-calendar-view .week-group > li {
    width: 14.28%;
    text-align: center;
    padding: 8px 0; }

.m-calendar-view .data-group {
  display: flex; }
  .m-calendar-view .data-group > li {
    width: 14.28%; }

.m-calendar-view .year-group, .m-calendar-view .mon-group {
  display: flex; }
  .m-calendar-view .year-group .view-item, .m-calendar-view .mon-group .view-item {
    width: 33.3%; }

.m-calendar-view .mon-grop .calendar-sel {
  border-radius: 20%; }

.m-calendar-view .searson-group {
  display: flex; }
  .m-calendar-view .searson-group .view-item {
    width: 50%; }

.m-calendar-view .last-year {
  width: 33.3%;
  margin: auto;
  text-align: center; }
  .m-calendar-view .last-year:hover span {
    background: #535be9;
    color: white; }

.m-calendar-view .calendar-today span {
  color: #FF6666;
  font-weight: bolder; }

.m-calendar-view .calendar-sel span {
  background: #FF6666;
  color: white; }

.m-calendar-view .m-time {
  color: #000;
  display: flex;
  justify-content: center;
  width: 100%;
  padding-top: 6px; }

.m-calendar-view .wacth-time {
  width: 64px;
  height: 24px;
  border: 1px solid #535be9;
  border-radius: 4px;
  text-align: center; }
