@charset "UTF-8";
.g-menu .menu-item {
  position: relative;
  padding-left: 20%; }

.g-menu .menu-par {
  font-size: 16px;
  height: 48px;
  line-height: 48px;
  display: flex;
  justify-content: space-between;
  padding-right: 16px;
  color: #535be9; }
  .g-menu .menu-par a {
    color: #535be9; }
  .g-menu .menu-par .par-icon {
    position: absolute;
    margin-left: -22px; }

.g-menu .menu-child {
  height: 38px;
  line-height: 38px; }
  .g-menu .menu-child a {
    color: black; }
  .g-menu .menu-child a:hover {
    color: #535be9; }

.g-menu .child-ul {
  overflow: hidden;
  transition: max-height .3s ease-out; }

.g-menu .j-slide_menu {
  cursor: pointer; }

.menu-par.active {
  background: #dcdcff; }

.menu-child.active a {
  color: #535be9; }

/* 菜单收缩时 */
.g-slideMenu.expand .g-menu li {
  position: relative; }

.g-slideMenu.expand .menu-par {
  justify-content: center;
  padding-left: 0;
  padding-right: 0; }
  .g-slideMenu.expand .menu-par .j-nav {
    display: none; }
  .g-slideMenu.expand .menu-par .j-slide_menu {
    display: none; }

.g-slideMenu.expand .par-icon {
  margin-left: 0; }

.g-slideMenu.expand .li-par:hover .child-ul {
  display: block;
  position: absolute;
  top: 0;
  left: 50px;
  width: 120px;
  background-color: #ffffff;
  box-shadow: 0px 3px 6px 0px rgba(0, 0, 0, 0.16); }

.g-slideMenu.expand .menu-par + .child-ul {
  display: none; }
