@charset "UTF-8";
/* 阴影 */
.g-notification .g-alertInfo {
  background-color: #ffffff;
  box-shadow: 1px 1px 6px 0px rgba(0, 0, 0, 0.16);
  border-radius: 4px; }

.g-notification {
  position: absolute;
  right: 20px;
  top: 10px;
  z-index: 20;
  text-align: right;
  width: 50%;
  overflow: hidden;
  padding: 0 6px; }
  .g-notification .g-alertInfo {
    display: inline-flex;
    padding: 10px;
    margin: 8px 0;
    align-items: center;
    min-width: 280px;
    justify-content: space-between;
    text-align: left; }

.g-alertInfo .txt {
  text-align: left; }

.g-alertInfo .warn {
  color: #ff9900; }

.g-alertInfo .error {
  color: #ff0000; }

.g-alertInfo .success {
  color: #06b04a; }

.g-alertInfo .m-alert-close {
  padding-left: 10px;
  cursor: pointer; }
  .g-alertInfo .m-alert-close:hover {
    color: #535be9; }
