@charset "UTF-8";
/* 阴影 */
.combobox .m-drop, .comTreeBox .m-drop, .active.combobox .m-combo-inp, .active.comTreeBox .m-combo-inp {
  background-color: #ffffff;
  box-shadow: 1px 1px 6px 0px rgba(0, 0, 0, 0.16);
  border-radius: 4px; }

.combobox .m-drop, .comTreeBox .m-drop, .active.combobox .m-combo-inp, .active.comTreeBox .m-combo-inp {
  box-shadow: 0px 1px 8px 0px rgba(0, 160, 233, 0.3);
  border-color: #acb1fe; }

.combobox, .comTreeBox {
  position: relative; }
  .combobox .m-combo-inp, .comTreeBox .m-combo-inp {
    display: flex;
    height: 34px;
    cursor: pointer;
    border: 1px solid;
    border-radius: 4px;
    border-color: #e3e3e3;
    align-items: center;
    background-color: #ffffff; }
    .combobox .m-combo-inp .m-inp, .comTreeBox .m-combo-inp .m-inp {
      width: 100%;
      box-sizing: border-box;
      border: none;
      outline: none;
      cursor: pointer;
      text-indent: .5em;
      height: 30px; }
    .combobox .m-combo-inp textarea.m-inp, .comTreeBox .m-combo-inp textarea.m-inp {
      resize: none;
      line-height: 1.5em;
      padding-top: 6px; }
    .combobox .m-combo-inp .j-slide, .comTreeBox .m-combo-inp .j-slide {
      padding: 0 8px;
      cursor: pointer; }
  .combobox .m-drop, .comTreeBox .m-drop {
    position: absolute;
    display: none;
    box-sizing: border-box;
    width: 100%;
    background-color: #ffffff;
    border-radius: 4px;
    border: solid 1px #ececec;
    padding: 8px;
    z-index: 10; }
    .combobox .m-drop .m-combo-item, .comTreeBox .m-drop .m-combo-item {
      padding: 8px;
      cursor: pointer; }
      .combobox .m-drop .m-combo-item:hover, .comTreeBox .m-drop .m-combo-item:hover {
        color: #535be9; }
  .combobox .m-drop.direction-up, .comTreeBox .m-drop.direction-up {
    bottom: 44px; }
  .combobox .m-drop.direction-down, .comTreeBox .m-drop.direction-down {
    margin-top: 10px; }
  .combobox .m-combo-item.active, .comTreeBox .m-combo-item.active {
    background-color: #acb1fe;
    border-radius: 2px; }
  .combobox .m-combo-item.activeClick, .comTreeBox .m-combo-item.activeClick {
    color: #535be9; }

.combobox .m-drop {
  overflow: auto; }

.no-fill.combobox .m-combo-inp, .no-fill.comTreeBox .m-combo-inp {
  border-color: #ff0000; }

.comTreeBox .m-combo-item {
  display: flex;
  justify-content: space-between;
  align-items: center; }

.comTreeBox .m-tree {
  overflow: auto; }

.item-inp {
  display: flex;
  align-items: center; }
  .item-inp .m-inp-tit {
    padding-right: 8px; }
    .item-inp .m-inp-tit:after {
      content: "："; }
