/* radio */
/**
* normal-btn:有背景的按钮 ，line-btn:线框按钮，dashed-btn：虚线按钮
* danger:颜色为红,primary：主题颜色，green：绿颜色
*/
.s-btn {
  display: inline-block;
  outline: none;
  padding: 6px 10px;
  cursor: pointer;
  position: relative;
  transition: all 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
  border-radius: 4px; }

.btn-clicked:after {
  content: "";
  position: absolute;
  top: -1px;
  left: -1px;
  right: -1px;
  bottom: -1px;
  opacity: 0.4;
  border-radius: inherit;
  border: 0 solid #001529;
  animation: buttonEffect .4s; }

.normal-btn {
  border: none;
  color: #fff; }
  .normal-btn.primary {
    background: #535be9; }
    .normal-btn.primary:hover {
      background: #979cf2; }
  .normal-btn.danger {
    background: #ef5326; }
    .normal-btn.danger:hover {
      background: #f48b6d; }
  .normal-btn.green {
    background: #06b04a; }
    .normal-btn.green:hover {
      background: #0cf76a; }

.line-btn {
  border: 1px solid;
  background: #fff; }
  .line-btn.primary {
    color: #535be9;
    border-color: #535be9; }
    .line-btn.primary:hover {
      border-color: #979cf2; }
  .line-btn.danger {
    color: #ef5326;
    border-color: #ef5326; }
    .line-btn.danger:hover {
      border-color: #f48b6d; }
  .line-btn.green {
    color: #06b04a;
    border-color: #06b04a; }
    .line-btn.green:hover {
      border-color: #0cf76a; }

@keyframes buttonEffect {
  to {
    opacity: 0;
    top: -6px;
    left: -6px;
    bottom: -6px;
    right: -6px;
    border-width: 6px; } }

/*描边效果 */
.s-dashBtn:before, .s-dashBtn:after {
  content: "";
  position: absolute;
  width: 0;
  height: 0;
  border: solid 2px transparent;
  z-index: 3;
  box-sizing: border-box;
  border-radius: inherit; }

.s-dashBtn {
  background: none;
  color: #535be9;
  position: relative; }
  .s-dashBtn:before {
    top: 0;
    left: 0;
    transition: border-color 0s ease-in .8s,width .2s ease-in .6s,height .2s ease-in .4s; }
  .s-dashBtn:after {
    right: 0;
    bottom: 0;
    transition: border-color 0s ease-in .4s,width .2s ease-in .2s,height .2s ease-in; }
  .s-dashBtn:hover:before {
    border-top-color: #535be9;
    border-right-color: #535be9;
    width: 100%;
    height: 100%;
    transition: border-color 0s ease-out 0s,width .2s ease-out 0s,height .2s ease-out .2s; }
  .s-dashBtn:hover:after {
    border-bottom-color: #535be9;
    border-left-color: #535be9;
    width: 100%;
    height: 100%;
    transition: border-color 0s ease-out .4s,width .2s ease-out .4s,height .2s ease-out .6s; }

